import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'getAriaSort',
})
export class AriaSortPipe implements PipeTransform {
  transform(value: string, sortColumn: string, sortDirection: string): string | null {
    const selectedDirection = sortDirection && sortDirection === 'desc' ? 'descending' : 'ascending';

    return sortColumn === value.toUpperCase() ? selectedDirection : null;
  }
}
